@font-face {
  font-family: "Roboto";
  src: url("../../fonts/Roboto-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../../fonts/Roboto-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
.page {
  position: relative;
}

.page__body {
  min-width: 320px;
  margin: 0;
  padding: 0 0 175px;
  font-style: normal;
  font-size: 14px;
  line-height: 18px;
  background-color: #201f2e;
  font-family: "Roboto", "Arial", sans-serif;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 30px 0;
  min-width: 320px;
}
@media (min-width: 768px) {
  .container {
    width: 100%;
  }
}
@media (min-width: 1024px) {
  .container {
    max-width: 1440px;
  }
}

.header {
  margin-bottom: 52px;
}

.header__wrapper {
  padding-top: 32px;
  position: relative;
}

.header__info-block {
  margin-bottom: 49px;
}

.header__title {
  margin: 0;
  color: #ffffff;
  font-size: 24px;
  line-height: 28px;
}

.header__date {
  color: #c4c1cf;
  font-size: 18px;
  line-height: 21px;
  margin: 0;
}

.header__logo {
  margin: 0;
}

.header__logo-link {
  position: absolute;
  right: -20px;
  top: 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  overflow-y: hidden;
}
.nav__list::-webkit-scrollbar {
  width: 0;
}
@media (min-width: 768px) {
  .nav__list {
    min-width: 384px;
  }
}

.nav__button {
  color: #c4c1cf;
  font-size: 18px;
  line-height: 21px;
  font-weight: 300;
  font-family: "Roboto", "Arial", sans-serif;
}
.nav__button:hover, .nav__button:focus {
  border: 1px solid #c4c1cf;
  border-radius: 32px;
}

.button {
  background-color: transparent;
  border: none;
  padding: 10px 15px;
  cursor: pointer;
}

.favourite-btn {
  position: absolute;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
  width: 40px;
  height: 40px;
}

.favourite-btn__img {
  fill: #c4c1cf;
}
.favourite-btn__img:active, .favourite-btn__img:focus {
  fill: #ff005c;
}

@media (min-width: 768px) {
  .card__wrapper {
    display: flex;
    justify-content: flex-start;
    gap: 32px;
    flex-wrap: wrap;
  }
  .card__wrapper--search {
    justify-content: center;
  }
}

.card__item {
  position: relative;
  margin-bottom: 32px;
  min-height: 332px;
}
.card__item--search {
  margin-bottom: 175px;
  flex-grow: 1;
}

.card__item-title {
  width: 100%;
  margin: 0;
  position: absolute;
  top: 40px;
  left: 50%;
  transform: translate(-50%);
  color: #ffffff;
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
}

.card__button-favourite {
  top: -10px;
  right: 20px;
}

.card__item-discount {
  position: absolute;
  top: 18px;
  left: 18px;
  padding: 20px 0;
  min-height: 80px;
  width: 80px;
  background-color: #0e0d13;
  border-radius: 24px;
}

.card__item-discount-subtitle {
  color: #ffffff;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  margin: 0;
  text-align: center;
}

.card__item-text {
  display: block;
  line-height: 16px;
  color: #c4c1cf;
  text-align: center;
}

.card__item-name {
  position: absolute;
  left: 32px;
  bottom: 24px;
}

.card__item-name-subtitle {
  color: #ffffff;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  margin: 0;
}

.card__item-name-text {
  display: block;
  line-height: 21px;
  color: #c4c1cf;
}

.search {
  background-color: #0e0d13;
  border-top-left-radius: 32px;
  border-top-right-radius: 32px;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2;
  width: 100%;
}

.search__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 30px;
  min-height: 96px;
}

.search__link-favourite {
  display: block;
  margin: 0;
  color: #c4c1cf;
  background-color: #44414e;
  font-size: 18px;
  line-height: 17px;
  padding: 15px 24px;
  text-decoration: none;
  border-radius: 32px;
}
.search__link-favourite:active {
  background-color: #353240;
  color: #e9e6f2;
}
.search__link-favourite:focus {
  outline: none;
  border: solid 1px #ff005c;
  color: #ff005c;
}

.search__button {
  padding: 14px;
}

.search__input {
  border-radius: 32px;
  border: none;
  outline: none;
  font-size: 18px;
  color: #c4c1cf;
  padding: 15px;
  background-color: #44414e;
}
.search__input--index {
  display: none;
}

.card-description {
  min-height: 400px;
  background-image: url("../img/blackberry-detail-mobile.jpg");
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  padding-top: 32px;
}

.card-description__back {
  display: block;
  width: 64px;
  height: 64px;
  border-radius: 16px;
  background-color: #44414e;
  outline: none;
}

.card-description__logo {
  margin-left: 25px;
  margin-top: 20px;
}

.card-description__info {
  position: absolute;
  background-image: url("data:image/svg+xml,%3Csvg width='384' height='203' viewBox='0 0 384 203' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M80.5 136.243C5.5 124.743 -18 92.7434 -26 45.2434L-41 0.243414H535.5C544.667 -1.92325 470 45.2434 441 111.5C409.518 183.426 337.5 196.243 296 202.243C254.5 208.243 155.5 147.743 80.5 136.243Z' fill='%23201F2E'/%3E%3C/svg%3E%0A");
  background-color: #0e0d13;
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  border-top-right-radius: 32px;
  transform: translate(0, 50%);
  bottom: -200px;
  left: 0;
  padding-top: 32px;
  min-height: 600px;
  width: 100%;
}

.card-description__subtitle {
  color: #ffffff;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  margin: 0;
}
.card-description__subtitle--cook {
  font-size: 18px;
  line-height: 21px;
  margin-bottom: 8px;
}

.card-description__text {
  display: block;
  line-height: 21px;
  color: #c4c1cf;
  margin-bottom: 32px;
}

.card-description__list {
  width: 100%;
  margin-bottom: 32px;
}

.card-description__item {
  display: flex;
  justify-content: space-between;
  white-space: nowrap;
}
.card-description__item td {
  font-size: 18px;
  line-height: 21px;
  color: #c4c1cf;
}
.card-description__item td:first-child {
  position: relative;
  display: flex;
  flex-grow: 1;
}
.card-description__item td:first-child::after {
  content: "";
  display: inline-block;
  width: 100%;
  margin: 5px 5px;
  border-bottom: 1px dashed #c4c1cf;
}

.card-description__recipe {
  margin: 0;
  font-size: 18px;
  line-height: 21px;
  color: #c4c1cf;
  text-align: start;
}

.card-description__button {
  top: -10px;
  right: 20px;
}